import {api} from '/ts/util/api';
import {Languages} from '/ts/util/Languages';
import Table from 'ts/ui/Table.ts';
import Panel from 'ts/ui/Panel';
import PopUp from 'ts/ui/Popup';
import opg from "../../../ts/opg";
import ViewDetail from "../press/ViewDetail";

api({
	getList: `mediaexhibtion/QueryAllEnableMediaExhibtionInfos?type=1`,
	types: 'meinfotype/GetMediaExhibtionInfoType?type=1'
});

const lpg = Languages.package;
const moduleName = lpg.moduleName;



opg.wrapPanel('#tbSearch', {
	title: `${moduleName}`,
	btnSearchId: null,
});

let selType = opg('#types').listBox({
	api : api.types,
	autoPrependBlank: false,
	onSelect: ()=>{
		tb.update({mediaExhitionInfoTypeId: selType.getValue() , pageIndex : 0});
	}
});


let tb: Table = opg('#tb').table({
	api: opg.api.getList,
	lazy:true,
	titleBar: {
		title: `${moduleName}${lpg.list}`,
	},
	columns: [
		{
			text: lpg.title,
			width: 300,
			src: 'title',
			render: (title , i , row)=>{
				return `<a href="javascript:void(0)" data-id="${row.id}">${title}</a>`
			}
		},
		{
			text: lpg.publishbegindate,
			width: 150,
			src: 'publishbegindate',
		},
		{
			text: `${lpg.digest}`,
			align:'left',
			src: 'digest',
		},

	],
	pagination: true,
});


tb.tbody.on('click' , 'a' , function () {
	new ViewDetail($(this).data('id') , Languages.package.readDetail);
});

